
import Vue from 'vue'
import Cell from 'vux/src/components/cell/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import Badge from 'vux/src/components/badge/index.vue'
import XDialog from 'vux/src/components/x-dialog/index.vue'
import AjaxPlugin from 'vux/src/plugins/ajax/index.js'
import ToastPlugin from 'vux/src/plugins/toast/index.js'
import dateFormat from 'vux/src/tools/date/format.js'

import { fetch, setUserInfo, getSingleUserInfo, isAuthJumpTo } from '@/api/base'

Vue.use(ToastPlugin)
Vue.use(AjaxPlugin)

export default {
name: 'index',
components: {
Cell,
Group,
XHeader,
Badge,
XDialog
},
data () {
return {
showScrollBox: false,
weID: getSingleUserInfo('user_wechatNumber') || '',
title: getSingleUserInfo('user_wechatNumber') || '',
joinDate: '未填',
userId: getSingleUserInfo('user_shortId') || '',
province: '未填',
isAuthed: 'false',
currentIdentity: getSingleUserInfo('user_identity')
}
},
methods: {
alert: function () {
this.showScrollBox = true
},
changeWeId: function () {
let reg = /^[0-9a-zA-Z_]{1,10}$/
if (reg.test(this.weID)) {
fetch('/api/v1/agents/wechatNumber', {wechatNumber: this.weID}, response => {
let data = response.data
let msg = data.msg || data.error || '请求错误'
if (data.code !== 0) {
this.$vux.toast.text(msg, 'middle')
return false
}
this.$vux.toast.text(msg, 'middle')
setUserInfo('user_wechatNumber', this.weID)
this.title = this.weID
}, error => {
console.log(error)
})
} else {
return this.$vux.toast.text('请输入10位英文、数字或下划线')
}
this.title = this.weID
this.showScrollBox = false
},
jump () {
isAuthJumpTo(this, '/Authentication')
}
},
created () {
if (getSingleUserInfo('user_' + this.currentIdentity.current + 'CreatedAt')) {
this.joinDate = dateFormat(new Date(getSingleUserInfo('user_' + this.currentIdentity.current + 'CreatedAt')), 'YYYY-MM-DD')
}
if (getSingleUserInfo('user_province') && getSingleUserInfo('user_city')) {
this.province = getSingleUserInfo('user_province') + ' ' + getSingleUserInfo('user_city')
}
this.isAuthed = getSingleUserInfo('user_auth') !== 3 ? 'false' : 'true'
}
}



